import { Container, Grid, SelectChangeEvent } from '@mui/material';
import moment from 'moment';
import React, { useState } from 'react';
import MultyTable from 'src/components/multyTable';
import { products } from 'src/mocks/products';

const cols = [
  { name: 'name', label: '' },
  { name: 'by', label: '' },
  { name: 'energy', label: ' (kcal)' },
  { name: 'fat', label: ' (kcal)' },
  { name: 'kor_fat', label: ' (.)' },
  { name: 'cabs', label: ' (.)' },
  { name: 'sugar', label: ' (.)' },
  { name: 'edodomes', label: '   (.)' },
  { name: 'protein', label: ' (.))' },
  { name: 'salt', label: ' (.)' },
  { name: 'code', label: ' -' }
];

export default function Products() {
  return (
    <Container maxWidth={false}>
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="stretch"
        spacing={3}
      >
        <Grid item xs={12}>
          <MultyTable data={products} title="" cols={cols} />
        </Grid>
      </Grid>
    </Container>
  );
}
